body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header.single-player {
 background-color: black
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.item {
    margin: 2px;
    display: inline-grid;
    align-self: auto;
    width: 14.3%;
    font-size: 0
  }
  
  .item-image {  
    width: 100%;
    opacity: .25;
    font-size: 0;
  }
  
  .collected {
    opacity: 1;
  }

  .trackerItems {
    background-color: black;
    font-size: 0;
  } 
 

  .restreamedTracker {
    width: 100%;    
    display: grid;
    background-color: black;
  }
  .tracker {
    display: grid;
    flex-wrap: wrap;
    width: 184px;    
    padding: 10px;
  }
  .trackerBorder {
    border-width: 2px;
    border-color: #736f6f;
    border-style: solid;
  }
  .trackerContainer {
    display: inline-flex;
    flex-wrap: wrap;
    width: 184px;
  }

  .allTrackers {
    display: flex;
    flex-wrap: wrap;
    height: auto;
  }

  .instructions {
    font-size: 12px
  }

  .hidden {
    display: none
  }

  a {
    color: white
  }

